import uniSection from '@/components/uni-section/uni-section.vue';
import uniIcons from '@/components/uni-icons/uni-icons.vue';
import uniList from '@/components/uni-list/uni-list.vue';
import uniListItem from '@/components/uni-list-item/uni-list-item.vue';
export default {
	data() {
		return {
			title: 'SQLite',
			extraIcon1: {
				color: '#4cd964',
				size: '22',
				type: 'compose'
			},
			extraIcon2: {
				color: '#4cd964',
				size: '22',
				type: 'spinner-cycle'
			},
			extraIcon3: {
				color: '#4cd964',
				size: '22',
				type: 'navigate'
			},
			moneyList: [],
			sumTitle:0
		};
	},
	components: {
		uniList,
		uniListItem,
		uniIcons,
		uniSection
	},
	onLoad() {
		//var me = this;
		this.openDB();
		this.initData();

		/* uni.sqlite.executeSql({
			name: tally,
			sql: 'SELECT a.id AS id,a.time AS time,a.money AS money,t.name AS typeName FROM tally a LEFT JOIN types t ON a.typeId = t.id WHERE a.time >= "2020-1-24 00:00:00" AND a.time < "2020-1-24 23:59:59"',
			success: function(data) {
				console.log(data)
			},
			fail: function(data) {
				console.log(data)
			}
		}) */
	},
	methods: {
		openDB: function() {
			plus.sqlite.openDatabase({
				name: 'tally',
				path: '_doc/tally.db',
				success: function(e) {
					plus.nativeUI.alert('打开数据库test.db成功 ');
				},
				fail: function(e) {
					//plus.nativeUI.alert('打开数据库test.db失败: ' + JSON.stringify(e));
				}
			});
		},
		initData2: function() {
			plus.sqlite.executeSql({
				name: 'tally',
				sql:
					'SELECT a.id AS id,a.time AS time,a.money AS money,t.name AS typeName FROM tally a LEFT JOIN types t ON a.typeId = t.id WHERE a.time >= "2020-1-24 00:00:00" AND a.time < "2020-1-24 23:59:59"',
				success: function(data) {
					console.log(data);
				},
				fail: function(data) {
					console.log(data);
				}
			});
		},
		initData: function() {
			var me = this;
			var sum = 0;
			plus.sqlite.selectSql({
				name: 'tally',
				sql: 'SELECT a.id AS id,a.time AS time,a.money AS money,t.name AS typeName FROM tally a LEFT JOIN types t ON a.typeId = t.id',
				success: function(data) {
					for (let i in data) {
						var bean = data[i];
						me.moneyList.push({
							id: bean.id,
							time: bean.time,
							money: '-'+bean.money,
							typeName: bean.typeName
						});
						sum = sum + bean.money;
					}
					me.sumTitle = sum;
				},
				fail: function(e) {
					plus.nativeUI.alert('查询SQL语句失败: ' + JSON.stringify(e));
				}
			});
		}
	}
};
